@import url(https://fonts.googleapis.com/css?family=Roboto:400,700&display=swap);
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    outline: 0;
}

html, body, #root{
    height: 100%;

}

body{
    background-color: #e5e6f0;
    -webkit-font-smoothing: antialiased;
}

body, input, button {
    font-family: Roboto, sans-serif;
}
#app{
    max-width: 1200px;
    margin: 0 auto;
    padding: 60px 30px;

    display: flex;
    flex-direction: row;
    align-items: flex-start;
}

#app main{
    margin-left: 30px ;
}

@media (max-width: 930px) {
    #app {
        flex-direction: column;
    }

    #app main {
        margin-left: 0px;
        margin-top: 30px;
    }

    #app aside {
        width: 100%;
    }
}
aside{
    width: 320px;
    background-color: #ffff;
    box-shadow: 0 0 14 0 rgba(0, 0, 0, 0.02);
    border-radius: 2px;
    padding: 30px 20px;
}

aside h1 strong{
    font-size: 20px;
    text-align: center;
    display: block;
    color: #333;
}


main {
    flex: 1 1;
}

main ul{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 20px;
    gap: 20px;
    list-style: none;
}

@media (max-width: 616px) {
    main ul {
        grid-template-columns: 1fr;
    }
}


li.dev-item {
    background: white;
    box-shadow: 0 0 14px 0 rgba(0, 0, 0, 0.02);
    border-radius: 2px;
    padding: 20px;
}

li.dev-item header {
    display: flex;
    flex-direction: row;
    align-items: center;
}

li.dev-item header img {
    width: 54px;
    height: 54px;
    border-radius: 50%;
}

li.dev-item header .user-info {
    margin-left: 10px;
}

.user-info strong {
    display: block;
    font-size: 16px;
    color: #333;
}

.user-info span {
    font-size: 13px;
    color: #999;
    margin-top: 10px;
}

li.dev-item p {
    color: #666;
    font-size: 14px;
    line-height: 20px;
    margin: 10px 0;
}

li.dev-item a {
    color: #8e4dff;
    font-size: 14px;
    text-decoration: none;
}

li.dev-item a:hover {
    color: #6931ca;
}

li.dev-item button {
    color: #8e4dff;
    background: none;
    border: none;
    font-size: 14px;
    text-decoration: none;
    cursor: pointer;
}

li.dev-item button:hover {
    color: #6931ca;
}
form{
    margin-top: 30px;
}

form .input-block + .input-block{
    margin-top: 20px;
}

form .input-group{
    margin-top: 20px;
    display: grid;
    grid-gap: 20px;
    gap: 20px;
    grid-template-columns: 1fr 1fr;
}

form .input-group .input-block {
    margin-top: 0;
}

form .input-block label{
    color: #acacac;
    font-size: 14px;
    font-weight: 700;
    display: block;
}

form .input-block input{
    color: #666;
    width: 100%;
    height: 32px;
    font-size: 14px;
    border: 0;
    border-bottom: 1px solid #eee;
}

form button[type=submit]{
    width: 210%;
    border: 0;
    margin-top: 30px;
    background: #8d3cde;
    border-radius: 5px;
    padding: 15px 20px;
    font-size: 16px;
    color: #fff;
    cursor: pointer;
    transition: background 0.5s;
}

form button[type=submit]:hover{
    background: #6931ca;
}

