#app{
    max-width: 1200px;
    margin: 0 auto;
    padding: 60px 30px;

    display: flex;
    flex-direction: row;
    align-items: flex-start;
}

#app main{
    margin-left: 30px ;
}

@media (max-width: 930px) {
    #app {
        flex-direction: column;
    }

    #app main {
        margin-left: 0px;
        margin-top: 30px;
    }

    #app aside {
        width: 100%;
    }
}